/* https://github.com/cyntler/hamburger-react */
.mobileNavBox {
  position: relative;
}

.mobileNav ul {
  display: flex;
  flex-direction: column;
  gap: 1em;
  list-style: none;
}

.mobileNav {
  position: absolute;
  top: 75px;
  right: 10px;
  width: 90vw;
  padding: 30px;
  background-color: #367da4;
  color:white;
  border-radius: 2px 2px 10px 10px;
}
@media screen and (min-width: 550px) {
  .mobileNavBox {
    display: none;
  }
}
